import React, { useEffect, useState } from 'react';
import { useAuth0 } from '@auth0/auth0-react';
import { Constantes } from '../../Constantes';

const ListGastoPlan = () => {

    const { user, isAuthenticated } = useAuth0();

    const [gastosPlan, setGastosPlan] = useState([]);

    useEffect(() => {

        let url = Constantes.api_gastos_plan + user.email;

        console.log(url);

        const getData = async (url) => {
            let res = await fetch(url,
                {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                });
            let data = await res.json();
            console.log(data);
            setGastosPlan(data);
        }

        getData(url);


    }, []);



  return (
    <div>

      <div className='bodySectionHome'>
                    {gastosPlan.length == 0
                        ? <p>Sin subcategorias por mostrar</p>
                        : gastosPlan.map(gasto =>
                            <div className='row_gasto_plan'>
                                <span>{gasto.subcategoria}</span>
                                <div>{gasto.valor}</div>
                            </div>
                        )
                    }
                </div>
    </div>
  )
}

export default ListGastoPlan;
